.description {
  color: #ffffff;
  background-color: #81b3d2;
  text-align: center;
  padding: 70px 0 40px;
}

.description__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  margin: 0 0 11px 0;
  text-transform: uppercase;
}

.description__number {
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
}

.description__subtitle {
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  margin: 17px 0 0 0;
  text-transform: uppercase;
}

