.advantages-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 26px 0 24px;
  background-color: #eeeeee;
  text-align: center;
}

.advantages-area__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  margin: 0 0 11px 0;
  text-transform: uppercase;
}

.advantages-area__number {
  font-size: 14px;
  font-weight: 700;
  line-height: 21px;
}

.advantages-area__subtitle {
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  margin: 17px 0 0 0;
  text-transform: uppercase;
}

